.franchise {
  padding-bottom: 50px;
  color: #fff;
  &__inner {
    margin-top: 40px;
    background-image: url(../img/franchise-bg.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 140px 90px;
    border-radius: 20px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: radial-gradient(107.97% 139.55% at 73.65% 35.83%, rgba(172, 205, 104, 0.5) 0%, rgba(92, 166, 73, 0.5) 48.44%, rgba(72, 121, 60, 0.5) 100%);
      border-radius: 20px;
    }
  }
  &__content {
    max-width: 750px;
    position: relative;
    z-index: 2;
  }
  &__subtitle {
    font-weight: 800;
  }
  &__text {
    font-size: 18px;
    line-height: 26px;
    margin-top: 40px;
    max-width: 420px;
  }
  &__btn {
    width: 196px;
    height: 58px;
    margin-top: 60px;
  }
}

.clinics2 {
  padding: 50px 0 70px;
  &__subtitle {
    font-size: 24px;
    line-height: 57px;
    margin-top: 5px;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 24px;
    margin-top: 30px;
  }
  &__item {
    background: #F8F8F8;
    border-radius: 20px;
    padding: 64px 25px 32px;
    &-text {
      font-weight: 500;
      font-size: 22px;
      line-height: 30px;
      margin-top: 54px;
    }
  }
}

.pluses {
  padding: 70px 0 60px;
  &-bg {
    padding: 90px 0 125px;
    background: radial-gradient(107.97% 139.55% at 73.65% 35.83%, #ACCD68 0%, #5CA649 48.44%, #48793C 100%);
  }
  &__title {
    color: #fff;
    text-align: center;
    max-width: 1380px;
    margin-right: auto;
    margin-left: auto;
  }
  &__text {
    font-size: 24px;
    line-height: 150%;
    color: #FFFFFF;
    margin-top: 20px;
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 24px;
    margin-top: 35px;
  }
  &__item {
    background: linear-gradient(144.08deg, #FFFFFF 35.54%, #E7FFC7 127.93%);
    box-shadow: 0px 4px 34px rgba(127, 227, 100, 0.3);
    border-radius: 20px;
    &-number {
      position: relative;
      font-size: 60px;
      color: #5CA649;
      height: 153px;
      width: 133px;
      display: flex;
      justify-content: center;
      align-items: center;
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url(../img/line.svg);
        background-repeat: no-repeat;
      }
    }
    &-text {
      font-size: 20px;
      line-height: 30px;
      margin-top: 25px;
      padding: 0 25px 40px;
    }
  }
}

.principle {
  padding: 60px 0;
  &__title {
    color: #1E2023;
    text-align: center;
  }
  &__text {
    font-size: 24px;
    line-height: 150%;
    margin-top: 30px;
    text-align: center;
  }
  &__items {
    margin-top: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 30px;
  }
  &__item {
    background: #F8F8F8;
    border-radius: 20px;
    &-img {
      border-radius: 20px;
      img {
        width: 100%;
        height: auto;
      }
    }
    &-desc {
      padding: 0 28px 45px;
      margin-top: 36px;
    }
    &-title {
      font-weight: 800;
      font-size: 24px;
      line-height: 28px;
      letter-spacing: 0.01em;
      color: #5CA649;
    }
    &-text {
      font-size: 16px;
      line-height: 26px;
      margin-top: 19px;
    }
  }
}

.professionals {
  padding: 60px 0 40px;
  overflow: hidden;
  &__title {
    color: #1E2023;
    text-align: center;
  }
  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 0.01em;
    margin-top: 26px;
  }
  &__items {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: calc(100% + 100px);
  }
  &__item {
    text-align: center;
    width: calc((100% / 5) - 100px);
    margin: 100px 100px 0 0;
    &-title {
      font-size: 20px;
      line-height: 30px;
      margin-top: 37px;
    }
  }
  &__btn {
    margin-right: auto;
    margin-left: auto;
    margin-top: 115px;
    width: 196px;
    height: 58px;
  }
}

.proposal {
  padding: 60px 0;
  &__bg {
    background: radial-gradient(107.97% 139.55% at 73.65% 35.83%, #ACCD68 0%, #5CA649 48.44%, #48793C 100%);
    padding: 93px 0 135px;
  }
  &__title {
    color: #fff;
    max-width: 1125px;
    text-align: center;
    margin-right: auto;
    margin-left: auto;
  }
  &__inner {
    display: flex;
    margin-top: 70px;
  }
  &__left {
    width: calc(68% - 15px);
  }
  &__right {
    width: calc(32% - 15px);
    margin-left: 30px;
    display: flex;
    flex-direction: column;
  }
  &__video {
    position: relative;
    padding-bottom: 63%;
    video {
      border-radius: 20px;
      pointer-events: none;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      -o-object-fit: cover;
      object-fit: cover;
    }
  }
  &__play {
    border-radius: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75));
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    pointer-events: none;
    &-btn {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      background: #FFFFFF;
    }
  }
  &__info {
    background: linear-gradient(144.08deg, #FFFFFF 35.54%, #E7FFC7 127.93%);
    box-shadow: 0px 4px 34px rgba(127, 227, 100, 0.3);
    border-radius: 20px;
    padding: 35px;
    margin-bottom: 28px;
    &-title {
      font-size: 92px;
      color: #5CA649;
    }
    &-text {
      font-size: 28px;
      line-height: 150%;
      margin-top: 5px;
    }
  }
  &__btn {
    width: 254px;
    height: 78px;
    margin: auto auto 0;
    svg {
      margin-left: -10px;
    }
    span {
      margin-left: 10px;
    }
  }
}

.estimation {
  overflow: hidden;
  padding: 90px 0 105px;
  background: #F8F8F8;
  &__title {
    max-width: 1230px;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    color: #1E2023;
  }
  .slider {
    margin-top: 60px;
  }
  .estimation-img {
    width: 760px;
    max-width: calc(100% - 10px);
    height: auto;
    position: relative;
    padding-bottom: 30%;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .swiper-scrollbar {
    margin-top: 70px;
    width: 100%;
  }
}

.investment {
  padding: 40px 0 60px;
  &__bg {
    background: radial-gradient(107.97% 139.55% at 73.65% 35.83%, #ACCD68 0%, #5CA649 48.44%, #48793C 100%);
    padding: 115px 0;
  }
  &__title {
    max-width: 1125px;
    text-align: center;
    margin-right: auto;
    margin-left: auto;
    color: #fff;
  }
  &__text {
    font-size: 24px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    margin-top: 15px;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    margin-top: 60px;
  }
  &__item {
    background: linear-gradient(144.08deg, #FFFFFF 35.54%, #E7FFC7 127.93%);
    box-shadow: 0px 4px 34px rgba(127, 227, 100, 0.3);
    border-radius: 20px;
    padding: 60px;
    &-title {
      font-size: 110px;
      color: #5CA649;
    }
    &-text {
      font-size: 40px;
      line-height: 150%;
      margin-top: 5px;
    }
  }
  &__info {
    margin-top: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__desc {
    font-size: 30px;
    line-height: 130%;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    margin-left: 12px;
    display: flex;
    align-items: center;
    span {
      background: #2B5122;
      border-radius: 50px;
      padding: 15px;
      margin-left: 12px;
      display: flex;
      justify-content: center;
    }
  }
}

.connection {
  color: #fff;
  padding: 120px 0 170px;
  background: radial-gradient(107.97% 139.55% at 73.65% 35.83%, #ACCD68 0%, #5CA649 48.44%, #48793C 100%);
  &__title {
    max-width: 1140px;
    span {
      font-weight: 800;
    }
  }
  &__full{
    grid-column: 1/4;
  }
  &__subtitle {
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: #9CDA8C;
    margin-top: 20px;
  }
  &__text {
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    margin-top: 60px;
    max-width: 740px;
  }
  &__form {
    margin-top: 40px;
    &-top {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 20px;
    }
    &-bottom {
      display: flex;
      align-items: center;
      margin-top: 80px;
    }
  }
  &__btn {
    width: 164px;
    height: 78px;
    font-size: 18px;
    margin-right: 70px;
  }
  &__check {
    label {
      letter-spacing: 0.01em;
      color: #FFFFFF;
      max-width: 430px;
    }
  }
}



@media screen and (max-width: 1600px) {
  .professionals {
    &__items {
      width: calc(100% + 70px);
    }
    &__item {
      margin: 100px 70px 0 0;
    }
  }
}


@media screen and (max-width: 1500px) {
  .clinics2 {
    &__items {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  .pluses {
    &__items {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  .principle {
    &__items {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }

  .proposal {
    &__info {
      &-title {
        font-size: 50px;
      }
      &-text {
        font-size: 24px;
      }
    }
  }

  .investment {
    &__item {
      &-title {
        font-size: 50px;
      }
      &-text {
        font-size: 24px;
      }
    }
  }
}


@media screen and (max-width: 1350px) {
  .professionals {
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 50px;
      width: 100%;
      margin-top: 100px;
    }
    &__item {
      margin: 0;
      width: 100%;
    }
  }

  .estimation {
    .estimation-img {
      width: 700px;
    }
  }

  .connection {
    &__form {
      &-top {
        grid-template-columns: 1fr;
      }
    }
    &__full{
      grid-column: 1/2;
    }
  }
}


@media screen and (max-width: 1200px) {
  .proposal {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 30px 0 0;
    }
  }

  .estimation {
    .estimation-img {
      width: 620px;
    }
  }
}


