    }
    &__items {
      grid-template-columns: 1fr;
      margin-top: 30px;
    }
    &__item {
      padding: 30px;
      &-title {
        font-size: 30px;
      }
      &-text {
        font-size: 18px;
      }
    }
    &__info {
      margin-top: 30px;
    }
    &__desc {
      font-size: 18px;
    }
  }

  .connection {
    padding: 30px 0;
    &__subtitle {
      font-size: 16px;
      margin-top: 15px;
    }
    &__text {
      max-width: 100%;
      margin-top: 15px;
      font-size: 16px;
    }
