.docs{
  padding-bottom: 110px;
  &__group{
    &:not(:last-child){
      margin-bottom: 60px;
    }
  }
  &__title{
    margin-bottom: 47px;
  }
  &__content{
    padding: 35px 40px 40px;
    border-radius: 20px;
    background-color: $whiteBg;
  }
  &__info{
    &:not(:last-child){
      margin-bottom: 41px;
    }
  }
  &__subtitle{
    margin-bottom: 23px;
    text-decoration: underline;
    letter-spacing: 0.01em;
    font-size: 24px;
    line-height: 125%;
    font-weight: 700;
  }
  &__contacts{
    &-group{
      &:not(:last-child){
        margin-bottom: 25px;
      }
    }
  }
  &__items{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
  }
  &__item{
    display: flex;
    &-icon{
      height: 24px;
      width: 16px;
      display: flex;
      align-items: center;
      flex-shrink: 0;
      margin-right: 15px;
      img{
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 1300px){
  .docs{
    &__items{
      grid-template-columns: 1fr 1fr;
    }
  }
}


@media screen and (max-width: 992px){
  .docs{
    padding-bottom: 55px;
    &__group{
      &:not(:last-child){
        margin-bottom: 30px;
      }
    }
    &__title{
      margin-bottom: 15px;
    }
    &__content{
      padding: 20px;
    }
    &__info{
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
    &__subtitle{
      margin-bottom: 15px;
      font-size: 20px;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 25px;
    }
  }
}
