.service-page{
  padding-bottom: 40px;
}

.service-doctor{
  padding: 56px 37px 37px 530px;
  position: relative;
  background-image: url(../img/service-bg.svg);
  background-size: cover;
  border-radius: 20px;
  margin-top: 207px;
  &__img{
    position: absolute;
    left: 125px;
    bottom: 0;
    width: 290px;
  }
  &__prof{
    margin-top: 9px;
  }
  &__desc{
    margin-top: 20px;
    font-weight: 500;
    font-size: 20px;
    line-height: 170%;
    &>*{
      line-height: inherit;
    }
  }
}

.service-info{
  margin-top: 100px;
  &__subtitle{
    margin-top: 35px;
  }
  &__text{
    margin-top: 30px;
    strong{
      font-weight: 700;
    }
  }
}

.service-main{
  padding-bottom: 50px;
  &__inner{
    padding: 57px 0 61px 89px;
    background: radial-gradient(105.35% 136.18% at 71.03% 34%, #ACCD68 0%, #5CA649 48.44%, #48793C 100%);
    border-radius: 20px;
    color: #fff;
    display: grid;
    grid-template-columns: 43% 57%;
    align-items: center;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__content{
    width: calc(100% + 50px);
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__subtitle{
    font-size: 17px;
    line-height: 150%;
  }
  &__title{
    margin: 20px 0;
  }
  &__btn{
    width: 147px;
    height: 58px;
    font-size: 16px;
  }
}

.service-nav{
  display: flex;
  justify-content: space-between;
  margin-top: 68px;
  &__left{
    max-width: 802px;
  }
  &__items{
    margin-top: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
  &__item{
    font-weight: 500;
    font-size: 18px;
    line-height: 133%;
    text-decoration: underline;
  }
  &__link{
    padding: 0 40px 0 30px;
    height: 58px;
    font-size: 16px;
    svg{
      margin-right: 20px;
    }
  }
}

.service-desc{
  padding: 60px 0;
  &__groups{
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
    &--gap100{
      gap: 100px;
    }
  }
}

.service-price{
  padding: 60px 0;
  &__inner{
    border-radius: 20px;
    padding: 55px;
    background-color: $whiteBg;
  }
  &__items{
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 100px;
  }
  &__item{
    padding: 20px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 18px;
    line-height: 167%;
    border-bottom: 1px solid rgba(#000, .05);
  }
  &__num{
    font-weight: 700;
    color: $primary;
  }
}

.all-services{
  padding: 107px 0 114px;
  background-color: $whiteBg;
  &__grid{
    margin-top: 60px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 65px 47px;
  }
  &__icon{
    height: 60px;
    display: block;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__subtitle{
    font-weight: 700;
    margin-top: 15px;
    display: block;
  }
  &__links{
    display: grid;
    grid-template-columns: 100%;
    gap: 6px;
    margin-top: 15px;
  }
  &__link{
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;

  }
}

.advice{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  border-radius: 20px;
  background-color: $whiteBg;
  &__left{
    padding: 38px 0 38px 54px;
  }
  &__title{
    display: flex;
    align-items: center;
    svg{
      margin-right: 20px;
    }
  }
  &__desc{
    margin-top: 20px;
  }
}


@media screen and (max-width: 1300px){
  .service-doctor{
    padding-left: 337px;
    &__img{
      left: 37px;
    }
  }

  .service-main{
    &__inner{
      padding: 30px 0 30px 40px;
      grid-template-columns: 48% 52%;
    }
    &__title{
      @media screen and (min-width: 1300px){
        font-size: 50px;
      }
    }
  }

  .service-nav{
    display: block;
    &__right{
      margin-top: 40px;
      display: flex;
    }
  }

  .service-price{
    &__items{
      gap: 0 50px;
    }
  }

  .all-services{
    &__grid{
      grid-template-columns: 1fr 1fr;
    }
  }

  .advice{
    grid-template-columns: 100%;
    gap: 20px;
    &__left{
      padding: 30px 45px 0;
    }
  }
}



@media screen and (max-width: 992px){
  .service-doctor{
    display: flex;
    flex-direction: column-reverse;
    padding: 20px;
    margin-top: 40px;
    border-radius: 10px;
    &__img{
      max-width: 100%;
      position: static;
    }
    &__desc{
      font-size: 16px;
    }
  }

  .service-info{
    margin-top: 50px;
    &__subtitle{
      margin-top: 15px;
    }
    &__text{
      margin-top: 20px;
    }
  }

  .service-main{
    padding-bottom: 25px;
    &__inner{
      padding: 20px;
      grid-template-columns: 100%;
      border-radius: 10px;
    }
    &__content{
      width: 100%;
    }
    &__subtitle{
      font-size: 15px;
    }
    &__title{
      max-width: 500px;
    }
    &__img{
      max-width: 500px;
    }
  }

  .service-nav{
    margin-top: 40px;
    &__items{
      grid-template-columns: 100%;
      gap: 15px;
    }
    &__link{
      width: 100%;
    }
  }

  .service-desc{
    padding: 30px 0;
    &__groups{
      gap: 25px;
    }
  }

  .service-price{
    padding: 30px 0;
    &__inner{
      padding: 20px;
      border-radius: 10px;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .all-services{
    padding: 50px 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 30px;
      margin-top: 30px;
    }
  }

  .advice{
    &__left{
      padding: 20px 15px 0;
    }
  }
}


@media screen and (max-width: 576px){
  .service-main{
    &__btn{
      width: 100%;
    }
  }

  .service-price{
    &__item{
      padding: 10px 0;
    }
    &__num{
      display: block;
      margin-top: 10px;
    }
  }
}
