.sale-page{
  padding-bottom: 100px;
  &__grid{
    margin-top: 45px;
  }
}

.sales{
  padding: 100px 0;
  .slider .swiper{
    &-slide{
      width: 627px;
      max-width: calc(100% - 10px);
      height: auto;
    }
  }
}

.sale-info{
  grid-column: 1/3;
  padding-left: 20px;
  background-color: $whiteBg;
  border-radius: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  &__content{
    padding: 36px 50px 36px 30px;
    position: relative;
  }
  &__img{
    position: relative;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: 20px;
    }
  }
  &__title{
    margin-bottom: 25px;
  }
  &__desc{
    max-width: 622px;
    strong{
      font-weight: 800;
    }
    ul{
      font-weight: 800;
      color: $primary;
      li{
        margin-bottom: 10px;
        &:before{
          content: ' ';
        }
      }
    }
    p,
    ul{
      &:not(:last-child){
        margin-bottom: 35px;
      }
    }
  }
  &__badge{
    z-index: 2;
    position: absolute;
    width: 153px;
    height: 153px;
    border-radius: 50%;
    color: #fff;
    font-size: 25px;
    line-height: 26px;
    font-weight: 800;
    background-color: $blue;
    top: 50%;
    right: 0;
    transform: translate(50%,-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    &:before{
      content: '';
      position: absolute;
      opacity: 0.3;
      border: 1px dashed #FFFFFF;
      width: 145.44px;
      height: 145.44px;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      border-radius: 50%;
    }
    span{
      font-size: 51px;
      line-height: 56px;
      display: block;
      width: 100%;
    }
  }
  &__badge2{
    position: absolute;
    padding: 0 14px;
    border-radius: 23px;
    height: 32px;
    display: flex;
    align-items: center;
    background-color: $red;
    font-weight: 800;
    font-size: 15px;
    color: #fff;
    top: 20px;
    right: 20px;
  }
  &__link{
    width: 147px;
    height: 58px;
    margin-top: 42px;
    font-size: 16px;
  }
}


@media screen and (max-width: 1300px){
  .sale-info{
    grid-template-columns: 100%;
    padding: 0;
    border-radius: 10px;
    &__content{
      padding: 20px 20px 70px;
    }
    &__img{
      border-radius: 10px;
      img{
        border-radius: 10px;
      }
    }
    &__badge{
      width: 100px;
      height: 100px;
      top: 100%;
      left: 50%;
      transform: translate(-50%,-50%);
      font-size: 18px;
      line-height: 19px;
      span{
        font-size: 40px;
        line-height: 42px;
      }
      &:before{
        width: 98px;
        height: 98px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .sale-page{
    padding-bottom: 50px;
  }

  .sales{
    padding: 50px 0;
    .slider .swiper{
      &-slide{
        width: 364px;
      }
    }
  }

  .sale-info{
    margin-top: 20px;
  }
}


@media screen and (max-width: 800px){
  .sale-page{
    &__grid{
      grid-template-columns: 100% !important;
    }
  }

  .sale-info{
    grid-column: 1/2;
  }
}


@media screen and (max-width: 576px){
  .sale-info{
    &__content{
      padding-bottom: 45px;
    }
    &__badge{
      width: 75px;
      height: 75px;
      font-size: 14px;
      line-height: 15px;
      span{
        font-size: 25px;
        line-height: 27px;
      }
      &:before{
        width: 73px;
        height: 73px;
      }
    }
    &__badge2{
      top: unset;
      bottom: 10px;
      right: 10px;
    }
  }
}
