.doctors{
  &__links{
    margin: 16px 0 42px;
  }
  &__tags{
    margin-bottom: 36px;
  }
}

.doctors-page{
  padding-bottom: 80px;
}

.doctor{
  padding-bottom: 70px;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 530px) 500px;
    gap: 30px;
    position: relative;
  }
  &__content{
    max-width: 951px;
  }
  &__img{
    max-width: 500px;
    position: absolute;
    width: 500px;
    right: 0;
    top: 0;
    img{
      width: 100%;
      display: block;
      border-radius: 20px;
    }
  }
  &__prof,
  &__experience{
    font-weight: 500;
    font-size: 18px;
    line-height: 144%;
    letter-spacing: 0.01em;
    color: $primary;
  }
  &__prof{
    margin-top: 27px;
  }
  &__experience{
    span{
      font-weight: 700;
    }
  }
  &__desc{
    margin-top: 27px;
  }
  &__info{
    margin-top: 50px;
    &-title{
      display: flex;
      align-items: center;
      margin-bottom: 3px;
      line-height: 192%;
      svg{
        margin-right: 10px;
      }
    }
    .link{
      margin-right: 30px;
    }
  }
  &__btn{
    margin-top: 50px;
    width: 319px;
    height: 58px;
    font-size: 16px;
    svg{
      margin-right: 20px;
    }
    &:hover{
      path{
        fill: none;
      }
    }
  }
}

.page-nav{
  padding: 7px 0 23px;
  background-color: $whiteBg;
  &__inner{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .link{
    @media screen and (min-width: 992px){
      margin-right: 90px;
    }
  }
}

.edu{
  padding: 70px 0 100px;
  &__grid{
    margin-top: 70px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 100px 140px;
  }
  &__year{
    font-size: 70px;
    line-height: 81%;
    color: $primary;
    margin-bottom: 30px;
  }
}

.licensy-block{
  overflow: hidden;
  .custom-block__inner{
    padding-bottom: 0;
  }
  .custom-block__top{
    @media screen and (min-width: 992px){
      margin-bottom: 70px;
    }
  }
}

.consult--doctor{
  .consult__inner{
    justify-content: space-between;
  }
  .consult__content{
    max-width: 1050px;
    flex-shrink: unset;
  }
  .consult__img{
    align-items: center;
    flex-shrink: 0;
    margin-left: 20px;
    img{
      border-radius: 300px;
    }
  }
  .consult__title{
    line-height: 117%;
  }
  .consult__form{
    max-width: 760px;
  }
}


@media screen and (max-width: 1300px) {
  .doctor {
    &__inner {
      grid-template-columns: calc(100% - 450px) 420px;
    }
    &__img{
      width: 420px;
    }
  }

  .edu{
    &__grid{
      gap: 40px;
    }
  }
}


@media screen and (max-width: 1200px) {
  .doctor {
    &__inner {
      grid-template-columns: 100%;
    }
    &__img{
      position: static;
      margin-top: 15px;
      max-width: 100%;
    }
  }

  .edu{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .consult--doctor{
    .consult__img{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .doctor{
    padding-bottom: 35px;
    &__prof,
    &__experience{
      font-size: 16px;
    }
    &__info,
    &__btn{
      margin-top: 30px;
    }
    &__img{
      img{
        border-radius: 10px;
      }
    }
  }

  .edu{
    padding: 35px 0 50px;
    &__grid{
      gap: 30px;
      margin-top: 40px;
    }
    &__year{
      font-size: 40px;
      line-height: 110%;
      margin-bottom: 15px;
    }
  }
}
