.feedback-page{
  padding-bottom: 50px;
  &__top{
    display: flex;
    justify-content: space-between;
    margin-top: 18px;
  }
  &__desc{
    max-width: 1035px;
  }
  &__btn{
    height: 58px;
    padding: 0 33px;
    flex-shrink: 0;
    font-size: 16px;
    margin-left: 20px;
    svg{
      margin-right: 12px;
    }
  }
  &__tags{
    margin-top: 32px;
  }
}


.feedbacks-block{
  padding: 70px 0 76px;
  background-color: $whiteBg;
  &__grid{
    margin-top: 73px;
  }
}


@media screen and (max-width: 1300px){
  .feedbacks-block{
    &__grid{
      grid-template-columns: 100% !important;
    }
  }
}


@media screen and (max-width: 992px){
  .feedbacks-block{
    padding: 35px 0;
    &__grid{
      margin-top: 35px;
    }
  }

  .feedback-page{
    &__top{
      display: block;
    }
    &__btn{
      margin: 20px 0 0;
      width: 100%;
    }
  }
}
