.for-doctors{
  padding-bottom: 30px;
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 90px;
  }
}

.edu-card{
  display: grid;
  grid-template-columns: 628px calc(100% - 658px);
  justify-content: space-between;
  gap: 30px;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 20px;
    }
  }
  &__content{
    padding: 45px 0;
    max-width: 865px;
    margin-left: auto;
  }
  &__desc{
    margin-top: 30px;
  }
  &__link{
    margin-top: 30px;
    width: 147px;
    height: 58px;
    font-size: 16px;
  }
}

.training {
  padding: 0 0 45px;
  &__btn {
    margin-top: 50px;
    width: 151px;
    height: 58px;
  }
}

.suitable-course {
  padding: 45px 0;
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 41px;
    margin-top: 65px;
  }
  &__item {
    background: #F8F8F8;
    border-radius: 20px;
  }
  &__img {
    img {
      width: 100%;
      height: auto;
    }
  }
  &__text {
    font-size: 22px;
    line-height: 30px;
    font-weight: 500;
    padding: 50px 30px;
  }
}

.schedule {
  padding: 45px 0 55px;
  &__items {
    margin-top: 50px;
  }
  &__item {
    display: flex;
    background: #F8F8F8;
    border-radius: 20px;
    &:not(:first-child) {
      margin-top: 30px;
    }
    &2 {
      display: none ;
      background: #F8F8F8;
      border-radius: 20px;
      margin-top: 3px !important;
      padding: 30px 30px 35px;
      &-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 46px;
        letter-spacing: 0.01em;
      }
      &-text {
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.01em;
        margin-top: 8px;
      }
    }
    &-right {
      display: flex;
      flex-grow: 1;
      padding: 27px 30px 27px 35px;
    }
    &-title {
      font-weight: 700;
      font-size: 20px;
      line-height: 130%;
      letter-spacing: 0.01em;
      color: #1E2023;
    }
    &-text {
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 0.01em;
      margin-top: 8px;
    }
  }
  &__block-img {
    position: relative;
    width: 497px;
    height: auto;
  }
  &__img {
    position: relative;
    padding-bottom: 69%;
    border-radius: 20px;
    overflow: hidden;
    &::after {
      content: '';
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 47.67%, rgba(0, 0, 0, 0.5) 100%);
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__img-text {
    position: absolute;
    z-index: 3;
    left: 30px;
    bottom: 30px;
    display: flex;
    align-items: center;
    span {
      font-weight: 800;
      font-size: 20px;
      line-height: 34px;
      color: #FFFFFF;
      margin-left: 12px;
    }
  }
  &__desc {
    max-width: 620px;
    &-bottom {
      margin-top: 32px;
      display: flex;
      align-items: center;
    }
    &-btn {
      width: 158px;
      height: 58px;
    }
  }
  &__price {
    font-size: 32px;
    line-height: 25px;
    font-weight: 500;
    letter-spacing: 0.01em;
    margin-left: 42px;
  }
  &__teacher {
    flex-grow: 1;
    margin-left: 15px;
    &-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &-title {
      font-weight: 400;
      font-size: 16px;
      line-height: 26px;
      letter-spacing: 0.01em;
      color: #5CA649;
    }
    &-subtitle {
      font-weight: 700;
      font-size: 20px;
      line-height: 25px;
      letter-spacing: 0.01em;
      margin-top: 5px;
      display: block;
    }
    &-img {
      display: block;
      width: 98px;
      height: 98px;
      flex-shrink: 0;
      border-radius: 50%;
      border: 1px dashed #5ca649;
      padding: 7px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-link {
      width: 147px;
      height: 58px;
      margin-left: auto;
      margin-top: 125px;
      &._show-detail--active{
        font-size: 0;
        &:after{
          content: '';
          font-size: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 1550px) {
  .schedule {
    &__item {
      display: block;
      &-left {}
    }
    &__block-img {
      width: 100%;
    }
    &__desc {
      width: calc(100% - 32% - 40px);
      max-width: 100%;
    }
    &__teacher {
      width: 32%;
      margin-left: 40px;
    }
  }
}


@media screen and (max-width: 1400px){
  .edu-card{
    grid-template-columns: 500px calc(100% - 530px);
    &__content{
      padding: 0;
    }
  }
}


@media screen and (max-width: 1200px){
  .for-doctors{
    &__items{
      gap: 45px;
    }
  }

  .edu-card{
    grid-template-columns: 100%;
    &__img{
      max-width: 500px;
    }
    &__content{
      max-width: 100%;
    }
    &__desc{
      margin-top: 15px;
    }
    &__link{
      margin-top: 15px;
    }
  }

  .suitable-course {
    &__items {
      gap: 25px;
    }
    &__text {
      padding: 30px 20px;
    }
  }

  .schedule {
    &__item-right {
      display: block;
    }
    &__desc {
      width: 100%;
    }
    &__teacher {
      width: 100%;
      margin: 30px 0 0;
      &-top {
        justify-content: start;
      }
      &-desc {
        margin-right: 50px;
      }
      &-subtitle {
        br {
          display: none;
        }
      }
      &-link {
        margin-right: auto;
        margin-left: unset;
        margin-top: 30px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .edu-card{
    gap: 15px;
    &__img{
      img{
        border-radius: 10px;
      }
    }
  }

  .training {
    padding: 0 0 15px;
    &__btn {
      margin-top: 30px;
    }
  }

  .suitable-course {
    padding: 15px 0;
    &__items {
      grid-template-columns: 1fr 1fr;
      margin-top: 30px;
    }
    &__text {
      font-size: 18px;
    }
  }

  .schedule {
    padding: 15px 0 30px;
    &__items {
      margin-top: 30px;
    }
    &__item {
      &2 {
        padding: 30px;
      }
    }
    &__item-right {
      padding: 30px;
    }
    &__price {
      font-size: 24px;
    }
  }
}


@media screen and (max-width: 768px) {
  .suitable-course {
    &__items {
      grid-template-columns: 1fr;
    }
  }
}


@media screen and (max-width: 576px) {
  .suitable-course {
    &__items {
      gap: 15px;
    }
    &__text {
      padding: 30px 15px;
    }
  }
  .schedule {
    &__item {
      &2 {
        padding: 30px 15px;
      }
      &-right {
        padding: 30px 15px;
      }
    }
    &__teacher {
      &-top {
        flex-direction: column-reverse;
        align-items: unset;
      }
      &-desc {
        display: block;
        margin: 0 0 15px;
      }
    }
    &__desc {
      &-bottom {
        display: block;
      }
    }
    &__price {
      margin: 25px 0 0;
    }
  }
}
