.advantage-card{
  &__icon{
    background: linear-gradient(225.98deg, #57D1CB 13.73%, #4CBEB9 100%);
    border-radius: 290px 290px 290px 7px;
    width: 250px;
    height: 237px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      background-image: url(../img/starts.webp);
      background-size: cover;
      background-position: center;
      width: 100%;
      height: 100%;
    }
    img{
      position: relative;
      z-index: 2;
    }
  }
  &__title{
    margin-top: 32px;
    color: #41A9A4;
  }
  &__desc{
    font-size: 15px;
    line-height: 160%;
    margin-top: 5px;
    letter-spacing: -0.02em;
    color: #3B4046;
  }
  &._purple{
    .advantage-card__icon{
      background: linear-gradient(224.28deg, #A38AE5 14.94%, #8B6DDB 98.93%);
    }
    .advantage-card__title{
      color: #8765DF;
    }
  }
  &._pink{
    .advantage-card__icon{
      background: linear-gradient(225.57deg, #F8888F 14.31%, #F47A82 100%);
    }
    .advantage-card__title{
      color: #FD6A74;
    }
  }
  &._blue{
    .advantage-card__icon{
      background: linear-gradient(225.43deg, #60A9FF 14.5%, #3B8CEA 100%);
    }
    .advantage-card__title{
      color: #3C85DB;
    }
  }
  &._orange{
    .advantage-card__icon{
      background: linear-gradient(223.01deg, #FFAB68 12.47%, #F79447 100%);
    }
    .advantage-card__title{
      color: #FE9846;
    }
  }
}

.service-card{
  min-height: 465px;
  background-size: cover;
  background-position: center;
  border-radius: 20px;
  display: flex;
  align-items: flex-end;
  padding: 42px 37px;
  position: relative;
  overflow: hidden;
  color: #fff;
  &__bg{
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transition: all .3s;
    position: absolute;
    display: block;
  }
  &:hover{
    color: #fff;
    .service-card__bg{
      opacity: .4;
    }
  }
  &__title{
    position: relative;
    z-index: 2;
    font-weight: 700;
    font-size: 26px;
    line-height: 104%;
    letter-spacing: -0.02em;
    display: inline-flex;
    align-items: center;
    min-height: 50px;
    padding-left: 20px;
    border-left: 2px solid #FFFFFF;
  }
}

.doctor-card{
  display: grid;
  grid-template-columns: 270px calc(100% - 270px);
  background-color: $whiteBg;
  border-radius: 50px;
  background-image: url(../img/brand4.svg);
  background-position: right 18px bottom 30px;
  background-repeat: no-repeat;
  &__img{
    height: 100%;
    border-radius: 50px;
    overflow: hidden;
    display: block;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
  }
  &__content{
    padding: 38px 45px 45px;
    display: flex;
    flex-direction: column;
  }
  &__name{
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;
    display: block;
    letter-spacing: -0.02em;
    color: $secondary;
  }
  &__prof,
  &__experience,
  &__desc{
    font-weight: 500;
    font-size: 16px;
    line-height: 162%;
    letter-spacing: -0.02em;
  }
  &__prof{
    margin-top: 16px;
  }
  &__experience{
    span{
      font-weight: 700;
    }
  }
  &__desc{
    margin-top: 24px;
    color: #969EAA;
  }
  &__buttons{
    margin-top: auto;
    padding-top: 20px;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0 32px;
    height: 58px;
    svg{
      margin-right: 12px;
    }
    &:not(:last-child){
      margin-right: 10px;
    }
  }
}

.doctor-card2{
  display: flex;
  flex-direction: column-reverse;
  .doctor-card{
    &__img{
      position: relative;
      overflow: hidden;
      padding-bottom: 96%;
      img{
        height: 133%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &__content{
      padding: 25px 15px 45px;
      flex-grow: 1;
    }
    &__desc{
      margin-top: 16px;
    }
    &__buttons{
      padding-top: 30px;
    }
  }
}

.feedback-card{
  background-color: #fff;
  padding: 37px 45px;
  border-radius: 20px;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__by{
    font-size: 24px;
    line-height: 192%;
    letter-spacing: -0.02em;
    color: $secondary;
  }
  &__for{
    font-size: 17px;
    letter-spacing: -0.02em;
    span{
      color: $primaryHover;
    }
  }
  &__by,
  &__for{
    font-weight: 700;
    line-height: 120%;
  }
  &__top2{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 15px;
  }
  &__left{
    display: flex;
    align-items: center;
  }
  &__site{
    margin-left: 30px;
    text-decoration-line: underline;
  }
  &__content{
    margin-top: 22px;
    transition: all .3s;
    &:not(._cut-text){
      .feedback-card__more{
        display: none;
      }
    }
  }
  &__text{
    transition: all .3s;
  }
  &__text,
  &__more{
    font-size: 17px;
    line-height: 165%;
    display: inline;
  }

  &__more{
    &._active{
      font-size: 0;
      &:before{
        content: '';
        font-size: 17px;
      }
    }
  }
  &__answer{
    display: flex;
    align-items: center;
    margin-top: 22px;
    &-icon{
      margin-right: 12px;
      flex-shrink: 0;
      width: 57px;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__course{
    font-weight: 700;
    font-size: 17px;
    line-height: 150%;
    margin-top: 28px;
    color: $primary;
  }
}

.feedback-card2{
  display: flex;
  flex-direction: column;
  &__img{
    height: 60px;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    padding-top: 20px;
    display: flex;
    align-items: center;
    margin-top: auto;
  }
  &__num{
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    margin-right: 20px;
  }
}

.note-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  &:hover{
    color: inherit;
  }
  &__title{
    font-weight: 700;
    font-size: 24px;
    line-height: 125%;
    letter-spacing: 0.01em;
    text-decoration-line: underline;
    margin-bottom: 28px;
    min-height: 60px;
    display: flex;
    align-items: flex-end;
    width: 100%;
  }
  &__img{
    display: block;
    width: 100%;
    position: relative;
    padding-bottom: 70%;
    background-size: cover;
    background-position: center;
    border-radius: 50px;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 47.67%, rgba(0, 0, 0, 0.5) 100%);
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__badge{
    position: absolute;
    z-index: 2;
    top: 27px;
    left: 34px;
    padding: 0 14px;
    height: 25px;
    display: flex;
    align-items: center;
    color: #fff;
    border-radius: 23px;
    white-space: nowrap;
    font-weight: 700;
    font-size: 13px;
    letter-spacing: -0.03em;
  }
  &__info{
    padding: 26px 34px;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-item{
      display: flex;
      align-items: center;
      font-weight: 500;
      font-size: 15px;
      line-height: 16px;
      letter-spacing: 0.01em;
      color: #fff;
    }
    &-icon{
      margin-right: 10px;
      flex-shrink: 0;
      width: 18px;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__doctor{
    width: 100%;
    margin-top: 28px;
    padding-left: 110px;
    position: relative;
  }
  &__name{
    font-weight: 700;
    font-size: 18px;
    line-height: 167%;
    letter-spacing: -0.02em;
    color: $secondary;
    margin-top: 3px;
  }
  &__desc2{
    font-size: 14px;
    line-height: 114%;
    letter-spacing: -0.02em;
    color: #2D343E;
  }
  &__avatar{
    border-radius: 50%;
    width: 80px;
    height: 80px;
    position: absolute;
    top: 0;
    left: 0;
    &-inner{
      border-radius: 50%;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &__desc{
    margin-top: 3px;
    font-size: 15px;
    line-height: 160%;
    color: #969EAA;
  }
  &__link{
    height: 58px;
    margin: 22px auto 0 0;
    font-size: 16px;
    padding: 0 32px;
  }
  &__smi{
    position: absolute;
    top: 20px;
    left: 20px;
    padding: 10px 20px;
    height: 58px;
    background: #FFFFFF;
    border-radius: 40px;
    display: block;
    img{
      height: 100%;
      display: block;
    }
  }
  &__list{
    margin-top: 30px;
    width: 100%;
    p{
      &:not(:last-child){
        margin-bottom: 6px;
      }
    }
  }
}

.note-card2{
  .note-card__desc{
    margin-top: 35px;
  }
}

.service-card2{
  .note-card__img:before{
    display: none;
  }
  .ba-slider{
    width: 100%;
  }
}

.social-card{
  position: relative;
  border-radius: 50px;
  overflow: hidden;
  &__img{
    padding-bottom: 100%;
    position: relative;
    img{
      pointer-events: none;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
  &__video{
    padding-bottom: 100%;
    &.video--played{
      &~.social-card__likes{
        display: none;
      }
    }
  }
  &__play{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($black,.7);
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    &-btn{
      width: 100px;
      height: 100px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(255, 255, 255, 0.4);
      backdrop-filter: blur(12px);
    }
  }
  &__likes{
    position: absolute;
    bottom: 22px;
    left: 22px;
    padding: 0 10px;
    display: flex;
    align-items: center;
    white-space: nowrap;
    background: #FFFFFF;
    border-radius: 23px;
    height: 32px;
    font-weight: 500;
    font-size: 15px;
    color: $primary;
    &:hover{
      cursor: pointer;
      opacity: .8;
    }
    &--active{
      font-weight: 800;
      .social-card__like{
        background-image: url(../img/heart2.svg);
      }
    }
  }
  &__like{
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    margin-right: 6px;
    background-image: url(../img/heart.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
}

.clinic-card{
  overflow: hidden;
  &__title{
    margin-top: 32px;
    font-weight: 700;
    font-size: 26px;
    line-height: 131%;
    letter-spacing: -0.02em;
    color: $secondary;
  }
  &__link{
    width: 148px;
    height: 58px;
    margin-top: 32px;
  }
  .map{
    height: 388px;
    border-radius: 20px;
    overflow: hidden;
    .map-widget-layout-view__ruler{
      opacity: 0;
    }
  }

  .address__items{
    grid-template-columns: 1.2fr 1fr;
    gap: 10px 20px;
    margin: 18px 0 0;
  }
  .address__item{
    font-size: 16px;
    line-height: 169%;
  }
}

.licensy-card{
  border-radius: 20px;
  background-color: $whiteBg;
  padding: 21px;
  overflow: hidden;
  display: block;
  &__inner{
    position: relative;
    padding-bottom: 142%;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.detail-card{
  &__title{
    margin-bottom: 30px;
    max-width: 657px;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 530px) 497px;
    gap: 33px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 20px;
    }
  }
}

.detail-card2{
  .detail-card__grid{
    grid-template-columns: 100%;
  }
}

.work-card{
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 310px) 310px;
    border-radius: 20px;
    overflow: hidden;
  }
  &__img{
    padding-bottom: 87%;
    position: relative;
    .ba-slider{
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      img{
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }
  }
  &__content{
    height: 100%;
    border-radius: 20px;
    background-color: $whiteBg;
    padding: 35px 20px 45px 30px;
    width: calc(100% + 20px);
    margin-left: -20px;
    position: relative;
    z-index: 2;
  }
  &__info{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__name{
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: 0.01em;
    margin-top: 9px;
    display: block;
  }
  &__avatar{
    flex-shrink: 0;
    margin-left: 4px;
    border-radius: 50%;
    width: 98px;
    height: 98px;
    border: 1px dashed $primary;
    padding: 7px;
    display: block;
    &-inner{
      border-radius: 50%;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &__items{
    margin-top: 47px;
  }
  &__item{
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    &:not(:last-child){
      margin-bottom: 15px;
    }
    &-icon{
      width: 24px;
      flex-shrink: 0;
      margin-right: 10px;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__time{
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 169%;
    margin-top: 44px;
    &-icon{
      width: 17px;
      flex-shrink: 0;
      margin-right: 17px;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__desc{
    margin-top: 34px;
    font-size: 15px;
    line-height: 160%;
  }
  .note-card__info-item{
    color: inherit;
    margin-top: 16px;
  }
}

.sale-card{
  border-radius: 20px;
  overflow: hidden;
  background-color: $whiteBg;
  display: flex;
  flex-direction: column;
  &__img{
    width: 100%;
    padding-bottom: 66%;
    background-size: cover;
    border-radius: 20px;
    position: relative;
    display: block;
  }
  &__badges{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 22px 19px;
  }
  &__badge{
    border-radius: 23px;
    height: 32px;
    display: flex;
    align-items: center;
    background: #FFFFFF;
    color: $primary;
    padding: 0 14px;
    white-space: nowrap;
    font-weight: 800;
    font-size: 15px;
    &--green{
      color: #fff;
      background: $primary;
    }
  }
  &__content{
    padding: 48px 71px 52px 41px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    font-weight: 400;
    width: 100%;
    display: block;
  }
  &__desc{
    width: 100%;
    margin: 20px auto 0 0;
    max-width: 400px;
  }
  &__info{
    width: 100%;
    margin-top: 20px;
    &-title{
      display: flex;
      align-items: center;
      font-size: 18px;
      line-height: 144%;
      svg{
        margin-right: 10px;
      }
    }
    .links{
      margin-top: -5px;
    }
    .link{
      margin-right: 30px;
      font-size: 16px;
    }
  }
  &__link{
    margin: 40px auto 0 0;
    padding: 0 32px;
    font-size: 16px;
    height: 58px;
  }
}


@media screen and (max-width: 1300px){
  .detail-card{
    &__grid{
      grid-template-columns: calc(100% - 370px) 350px;
      gap: 20px;
    }
  }
}


@media screen and (max-width: 992px){
  .service-card{
    min-height: 368px;
  }

  .doctor-card{
    display: flex;
    flex-direction: column;
    &__img{
      position: relative;
      overflow: hidden;
      padding-bottom: 96%;
      img{
        height: 133%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &__content{
      padding: 25px 15px 45px;
      flex-grow: 1;
    }
    &__desc{
      margin-top: 16px;
    }
    &__buttons{
      padding-top: 30px;
    }
  }

  .detail-card{
    &__title{
      margin-bottom: 20px;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      order: -1;
      img{
        border-radius: 10px;
      }
    }
  }

  .work-card{
    &__wrap{
      grid-template-columns: 100%;
      border-radius: 10px;
    }
    &__content{
      border-radius: 10px;
      width: 100%;
      margin: -10px 0 0 0;
      padding: 20px;
    }
    &__desc{
      margin-top: 20px;
    }
    &__info{
      flex-direction: column-reverse;
      align-items: unset;
    }
    &__avatar{
      margin: 0 0 10px;
    }
    &__name{
      br{
        display: none;
      }
    }
    &__items,
    &__time{
      margin-top: 25px;
    }
    &__item{
      font-size: 16px;
    }
  }

  .sale-card{
    &__content{
      padding: 20px;
    }
    &__link{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .doctor-card{
    &__name{
      font-size: 20px;
    }
    &__buttons{
      flex-wrap: wrap;
    }
    &__btn{
      width: 100%;
      &:not(:last-child){
        margin: 0 0 10px;
      }
    }
  }

  .feedback-card{
    padding: 25px 15px 35px;
    &__top,
    &__top2{
      display: block;
    }
    &__for{
      margin-top: 10px;
    }
    &__date{
      margin-top: 10px;
    }
    &__text,
    &__more,
    &__course{
      font-size: 15px;
    }
    &__answer{
      align-items: unset;
      display: block;
      &-icon{
        margin: 0 0 10px;
        display: block;
      }
    }
  }

  .note-card{
    &__doctor{
      flex-direction: column-reverse;
      align-items: unset;
    }
    &__avatar{
      margin: 0 0 10px;
    }
    &__desc{
      margin-top: 10px;
      padding: 0;
    }
    &__info{
      padding: 15px;
    }
    &__img{
      border-radius: 10px;
    }
    &__badge{
      left: 15px;
    }
    &__title{
      font-size: 18px;
      min-height: 45px;
    }
  }

  .clinic-card{
    grid-template-columns: 100%;
    border-radius: 10px;
    &__content{
      border-radius: 0;
      padding: 20px;
    }
    &__map{
      order: -1;
      position: relative;
      padding-bottom: 100%;
      .map{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        margin: 0;
      }
    }
  }

  .social-card{
    border-radius: 20px;
    &__play{
      &-btn{
        width: 45px;
        height: 45px;
        svg{
          width: 12px;
          height: auto;
        }
      }
    }
    &__likes{
      bottom: 5px;
      left: 5px;
      padding: 0 7px;
      font-size: 12px;
      height: 22px;
    }
    &__like{
      width: 13px;
      height: 13px;
      background-size: 100% auto;
      margin-right: 3px;
    }
  }
}
